.formations {
  .formationsContent {
    width: 100%;
    display: grid;
    min-height: $minHeight;
    grid-template-columns: 1fr;
    grid-template-rows: 50px 585px;
    grid-template-areas: 
      "1"
      "2";
  }

  .radioDisplay {
    grid-area: 1;
    border-radius: $borderRadius;
    display: flex;
    width: 99%;
    padding: 1rem 2rem;
    margin: 0 auto;
    background: $color-1;
    user-select: none;
    box-shadow: $shadow;

    li {
      color: $color-3;
      font-size: 1.1rem;
      text-transform: uppercase;
      padding: 0 1rem;
      flex: 1;
      z-index: 2;
      @include verticalCenter;

      input[type="radio"]{
        visibility: hidden;
        position: relative;

        &:before {
          content:'';
          visibility: visible;
          position: absolute;
          top: -4px;
          left: -12px;
          height: .9rem;
          width: .9rem;
          border-radius: 50%;
          border: 2px solid $color-4;
          cursor: pointer;
          transition: 0.3s;
        }
        &:after {
          content:'';
          visibility: visible;
          position: absolute;
          top: -4px;
          left: -12px; 
          height: .9rem;
          width: .9rem;
          border-radius: 50%;
          cursor: pointer;
          transition: 0.3s;
          background: $color-4;
          transform: scale(0);
          transform-origin: center;
        }
      }
      input:checked:after {
        transform: scale(1.17) translate(-25%, -25%);
        top: 2px;
        left: -6px;
      }
      input:checked + label {
        color: $color-4;
      }
      label {
        cursor: pointer;
      }
    }
  }
  .projects {
    grid-area: 2;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    .project {
      background: $color-2;
      border-radius: $borderRadius;
      margin: 0.5rem 0 0 0;
      text-align: center;
      width: 32%;
      max-height: 50%;
      padding: .3rem;
      box-shadow: $shadow;
      display: grid;
      grid-template-rows:  15% 10% 53% 22%;
      grid-template-areas: 
        "1"
        "2"
        "3"
        "4";
    }
   
    h3 {
      grid-area: 1;
      font-family: $font-2;
      font-size: 1.5rem;
      @include verticalCenter;
      justify-content: center;
      display: flex;

    }
    h4 {
      grid-area: 2;
      font-family: $font-2;
      font-size: 1rem;
      color:rgb(70, 73, 83);
      @include verticalCenter;
      justify-content: left;
      display: flex;

    }
    img {
      grid-area: 3;
      object-fit: cover;
      margin: 0.4rem auto;
      margin-top: 1rem;
      margin-bottom: 0.8rem;
      width: 100%;
      height: 80%;
      box-shadow: 0 2px 6px #333;
      border-radius: 4px;
      cursor: pointer;
      @include verticalCenter;
      display: flex;
    }
    .infos {
      grid-area: 4;
      color: $color-4;
      cursor: pointer;
      font-size: 1.5rem;
      @include verticalCenter;
      transition: .2s;
      justify-content: center;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows:1fr 1fr;
      grid-template-areas:
      "1" "2";

      
      img{
        grid-area: 1;
        object-fit: cover;
        margin: 0 auto;
        width: 40%;
        height: 40%;
        box-shadow: 0 2px 6px #333;
        border-radius: 4px;
        cursor: pointer;
        @include verticalCenter;
       
        }
        i{
          grid-area: 2; 
               
        }
        &:hover {
          transform: scale(1.1);
        }
    }
    .showInfos {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: rgba(0, 0, 0, 0.507);
      animation: popup .3s ease forwards ;
      transform-origin: center;

      .infosContent {
        position: relative;
        top: 25%;
        width: 600px;
        margin: 0 auto;
        color: $color-5;
        background: $color-1;
        border-radius: $borderRadius;
        padding: 2rem;
        box-shadow: 0 10px 10px #333;
        animation: popup .6s ease forwards;
        transform: scale(0);
        transform-origin: center;

        @keyframes popup {
          to {
            transform: scale(1);
          } 
        }
      }
      .head {
        display: flex;
        justify-content: space-around;
        margin: 1rem 0;
      }

      h2 {
        text-align: left;
        margin-bottom: 1rem;
        font-size: 1.4rem;
      }
      .button {
        cursor: pointer;
        color: $color-1;
        background: $color-5;
        border-radius: $borderRadius;
        padding: 0.5rem;
        transition: .2s;
        &:hover {
          background: $color-4;
        }
      }
      
      .text {
        text-align: justify;
        padding-bottom: 1rem;
      }
      .return {
        margin: 1rem auto;
        width: 300px;
      }
    }
  }
}


@media (max-width: 1000px) and (min-width: 851px){
  .formationsContent {
    .project {
      width: 32% !important;
     h3{
       font-size: 1.3rem;
     }
     h4{
       font-size:.8rem ;
     }
  }
 
    .infosContent {
      width: 100% !important;
    }
  }
}

@media (max-width: 850px) and (min-width: 651px){
  .formationsContent {
    .project {
      width: 49% !important;
     
  }
 
    .infosContent {
      width: 100% !important;
    }
  }
}

@media (max-width: 650px) and (min-width: 521px){
  .formationsContent {
    .project {
      width: 49% !important;
      h4{
      font-size:1rem;
    }
  }
    .infosContent {
      width: 100% !important;
    }
  }
}
@media (max-width: 520px){
  .radioDisplay {
    display: none !important;
  }
  .formationsContent {
    grid-template-rows: 1fr !important;

    .project {
      grid-template-rows: 30px 30px 200px 40px 20px!important;
      width: 98% !important;
      height:370px;
      img {
        height: 100% !important;
        
      }
      .infos{
        transform: translateY(1rem);
      }
      .showInfos{
          .infosContent{
            zoom:75%;
           
          }
      }
      
      

    }
  }
}