.knowledges {
  .knowledgesContent {
    min-height: $minHeight;
    width: 100%;
    position: relative;
    overflow: hidden;
    display: grid;
    grid-template-columns: 38% 22% 40%;
    grid-template-rows: 50% 20% 30%;
    grid-template-areas:
      "L E E"
      "Q E E"
      "H H O";

    .logicielsLanguages,
    .expérience,
    .otherSkills,
    .hobbies,
    .qualités {
      background: $color-2;
      border-radius: $borderRadius;
      padding: .8rem;
    }

    h3{
      font-size: 1.2rem;
      margin-bottom: 1rem;
      margin-top: .2rem;
      font-family: $font-1;
      color: $black;
      text-transform: uppercase;
    }

    .logicielsLanguages {
      grid-area: L;
      margin-bottom: .5rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr;
      grid-template-areas:
        "L"
        "F";

      .logicielsDisplay {
        grid-area: L;
      }

      .langues {
        grid-area: F;
        
        h3{
          margin-bottom:-0.1rem;
          margin-top:1.5rem;
        }
    

        li {
          margin-bottom: .5rem;
          margin-top: 0.5rem;
        }
      }

      h3 {
        margin-bottom: 1rem;

      }

      .years {
        font-size: .65rem;
        display: flex;
        justify-content: space-between;
        color: $white;

        span {
          width: 36%;

          &:nth-child(2) {
            text-align: center;
            width: 50%;
          }

          &:nth-child(3) {
            text-align: center;
            width: 60%;
          }

          &:nth-child(4) {
            text-align: right;
            width: 60%;
          }

          &:nth-child(2):before,
          &:nth-child(3):before {
            content: "";
            background: rgba(180, 179, 179, 0.5);
            position: absolute;
            height: 146px;
            width: 1px;
            transform: translate(9px, 19px);

          }
        }
      }

      .logicielsList {
        padding-top: .5rem;

        li {
          color: $color-1;
        }

        .progressBar {
          height: .4rem;
          background: transparent;
          border-radius: 50px;
          margin-top: .2rem;
          position: relative;

          &:after {
            content: "";
            position: absolute;
            background: $color-3;
            border-radius: 50 px;
            height: .4rem;
            width: 0;
            animation: line 2s ease-out forwards;

            @keyframes line {
              to {
                width: 100%;
                color:$color-5;
              }
            }
          }
        }
      }


    }

    .qualités {
      grid-area: Q;
      padding-left: 1rem;
      padding-bottom: 1rem;
      margin-bottom: 0.5rem;

      h3 {
        margin-top: -0.3rem;
        margin-bottom: 0.4rem;
      }

      .listqualités {
        display: flex;
        width: 80%;
        height: 100%;
        margin: 0 auto;
        justify-content: space-between;

        ul {
          height: 80%;
          display: grid;

          i {
            color: $color-5;
            margin-right: 1.5rem;
          }
        }
      }
    }

    .expérience {
      grid-area: E;
      margin: 0 0 .5rem .5rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 10% 1fr 1fr;
      grid-template-areas:
        "1"
        "2"
        "3";

      h3 {
        grid-area: 1;
        margin-bottom: -0.5rem;

      }

      h4 {
        font-size: 1rem;
        color: $color-5;
        margin-top: -0.5rem;
      }

      h5 {
        color: $color-3;
        margin-bottom: 0.05rem;
        padding: 0.5rem 0 0.5rem 1rem;
      }

      p {
        text-align: justify;
        padding: 0 1rem;
        font-size:0.9rem;
        margin-top: -0.5rem;
      }

      .exp-1 {
        grid-area: 2;
      }

      .exp-2 {
        grid-area: 3;
      }
    }

    .otherSkills {
      grid-area: O;
      padding-left: 1.5rem;

      .list {
        display: flex;
        width: 80%;
        height: 100%;
        margin: 0 auto;
        justify-content: space-between;

        ul {
          height: 80%;
          display: grid;

          i {
            color: $color-5;
            margin-right: 1.5rem;
          }
        }
      }
    }

    .hobbies {
      grid-area: H;
      margin-right: 0.5rem;
      display:grid;
      grid-template-columns: 30% 1fr;
      grid-template-rows: 30% 1fr;
      grid-template-areas:
      "1 1"
      "2 3";
     
      h3{
        grid-area: 1;
      }
      ul {
        grid-area: 2;
        height: 100%;
        display: grid;
        
  
        li {
          i {
            position: absolute;
            color: $color-5;
            margin-left: 1rem;

          }

          span {
            position: absolute;
            margin-left: 2rem;
            transform: translateX(2.2rem);
          }
        }
      }

      .infos {
        grid-area: 3;
        position: absolute;
        margin-left: 13rem;
        transform: translateX(-1rem);
        color: rgb(179, 199, 63);
        cursor: pointer;
        display:grid;
        transition: .2s;
        width:35%;
       
        
        &:hover {
          transform: scale(1.1);
        }

        i {
          position: absolute;
          color: $color-5;
          margin-left: -2rem;
        }
      }

      .showInfos {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0rem;
        right: 0;
        padding-bottom: 35rem;
        background: rgba(0, 0, 0, 0.507);
        animation: popup .3s ease forwards;
        transform-origin: center;
        z-index: 1;



        .infosContent {
          position: relative;
          top: 25%;
          width: 500px;
          height: 600px;
          margin: 0 auto;
          color: $color-5;
          background: $color-1;
          border-radius: $borderRadius;
          padding: 2rem;
          box-shadow: 0 10px 10px #333;
          animation: popup .6s ease forwards;
          transform: scale(0);
          transform-origin: center;
          display: grid;
          grid-template-rows: 15% 50% 10% 17% 8%;
          grid-template-areas:
            "1"
            "2"
            "3"
            "4"
            "5";

          @keyframes popup {
            to {
              transform: scale(1);
            }
          }

          h3 {
            grid-area: 1;
            font-family: $font-2;
            font-size: 1.2rem;
            @include verticalCenter;
            justify-content: center;
            margin-top: 10.5rem;
            
            color:$color-3;
          }

          
          
            .image1{
              grid-area: 2;
              object-fit: cover;
              margin: 0 auto;
              margin-top: -0.5rem;
              margin-bottom: 0.3rem;
              width: 400px;
              height: 445px;
              box-shadow: 0 2px 6px #333;
              border-radius: 4px;
              cursor: pointer;
              @include verticalCenter;
              zoom:80%;
            
          }
          
          .image2{
            grid-area: 2;
            object-fit: cover;
            margin: 0 auto;
            margin-top: 1rem;
            margin-bottom: 0.8rem;
            width: 450px;
            height: 450px;
            box-shadow: 0 2px 6px #333;
            border-radius: 4px;
            cursor: pointer;
            @include verticalCenter;
            zoom:80%;
          
        }

          h4 {
            grid-area: 3;
            font-family: $font-2;
            font-size: 1rem;
            color: rgb(82, 104, 185);
            @include verticalCenter;
            justify-content: center;
            color:$color-2;
          }
          
          p {
            grid-area: 4;
            
            font-family: $font-2;
            font-size: 0.8rem;
            color: rgb(131, 135, 148);
            @include verticalCenter;
            text-align: justify;
            justify-content: center;
             .p1{
              width: 320px;
             }
             .p2{
              width: 360px;
             }
            }
    
          .button {
            grid-area: 5;
            cursor: pointer;
            color: $color-1;
            background: $color-5;
            border-radius: $borderRadius;
            transition: .2s;
            padding-bottom: 1.4rem;
            padding-top: 0.5rem;
            font-size:0.8rem;
            text-align:center;

            &:hover {
              background: $color-4;
            }
          }

          .text {
            text-align: justify;
          }

          .return {
            margin:1.4rem auto;
            width: 180px;
            
          }
        }



      }

    }

  }
}



      @media screen and (max-width: 1150px) {
        .knowledgesContent {
          .expérience{
          .exp-1{
            p{
              font-size: 0.71rem;
            }
          }
          .exp-2{
            p{
              font-size: 0.71rem;
            }
          }
        }
          h3 {
            font-size: 1.2rem !important;
          }

          .logicielsList {
            padding-top: .2rem !important;
          }

          

          .otherSkills,
          .hobbies, .qualités {
            font-size: .8rem;
          }
        }
      }

      @media screen and (max-width: 1000px) {
        .knowledgesContent {
         
          h4 {
            font-size: 0.9rem !important;
          }
         
          h5 {
            font-size: .6rem !important;
            padding: .2rem 1rem !important;
          }
        }
      }

      @media screen and (max-width: 500px) {
        .knowledgesContent {
          display: grid !important;
          grid-template-columns: 1fr !important;
          grid-template-rows: 1fr 1fr 180px 180px 180px !important;

          .logicielsLanguages {
            grid-area: 1 !important;
            height:350px !important;
          }

          .expérience {
            grid-area: 2 !important;
            margin-left: 0 !important;
            height:350px !important;
            
          }

          .qualités{
            grid-area: 3 !important;
            margin-top:0 !important;
          }

          .otherSkills {
            grid-area: 4 !important;
            margin-right: 0 !important;
          }

          .hobbies {
            grid-area: 5 !important;
            margin-top: .5rem !important;
            .showInfos{
              zoom:70%;
            }
          }
        }
      }
    
      @media screen and (max-width: 400px) {
        .knowledgesContent {
          display: grid !important;
          grid-template-columns: 1fr !important;
          grid-template-rows: 1fr 1fr 180px 180px 180px !important;

          .logicielsLanguages {
            grid-area: 1 !important;
            height:450px !important;
          }

          .expérience {
            grid-area: 2 !important;
            margin-left: 0 !important;
            height:420px !important;
            
          }

          .qualités{
            grid-area: 3 !important;
            margin-top:0 !important;
          }

          .otherSkills {
            grid-area: 4 !important;
            margin-right: 0 !important;
          }

          .hobbies {
            grid-area: 5 !important;
            margin-top: .5rem !important;
            .showInfos{
              .infosContent{
                width:370px;
              }
            }
          }
        }
      }